html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    color: #323232;
}
.container {
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 30px;
}

.app {
    display: flex;
    flex-direction: column;
}
